h1 {
  text-align: center;
}

.cards__container {
  display: flex;
  flex-flow: flex;
  align-items: center;
  max-width: 1150px;
  width: 70%;
  margin: 0 auto;
}
.cards__item {
  width: 250px;
}
.cards__item__text {
  color: #252e48;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
}

.more-info {
  text-align: center;
}
.info {
  text-align: center;
  text-decoration: none;
  font-size: 23px;
  color: black;
  margin: 10px auto 10px auto;
}
.info:hover {
  color: #2596be;
}
.info:visited {
  color: #026186;
}

.push-button {
  color: black;
  font-size: 25px;
  padding: 10px;
  text-align: center;
  background-color: #2596be;
  margin: 0 auto 20px;
  border-radius: 10px;
  border: 2px solid #2596be;
  transition: all 0.3s;
  text-decoration: none;
}
.push-button:hover {
  transition: all 0.3s;
  color: #f3f3f3;
}

@media only screen and (max-width: 768px) {
  .cards__item {
    width: 500px;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}
