.misionvision {
  display: inline;
  justify-content: center;
}
.mision-warp {
  width: 600px;
  margin: 20px auto 10px;
  border-radius: 10px;
  box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;
}
.mision-title {
}
.mision-content {
  text-align: justify;
  margin: 5px 7px 5px 7px;
  font-size: 20px;
}

.vision-warp {
  width: 600px;
  margin: 40px auto 10px;
  border-radius: 10px;
  box-shadow: rgba(3, 102, 214, 0.3) 0px 0px 0px 3px;
}
.vision-title {
}
.vision-content {
  text-align: justify;
  margin: 5px 7px 5px 7px;
  font-size: 20px;
}
.containers {
  display: flex;
  align-items: center;
  justify-content: center;
}
.container-warp {
  width: 15%;
  height: 500px;
  border-radius: 15px;
  margin: 50px 50px 50px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.profile-picture {
  display: flex;
  align-items: center;
}
.name {
  background-color: mintcream;
  margin-left: 25%;
}
.img {
  width: 75px;
  height: 75px;
  border-radius: 50px;
}
.abouthim {
  margin: 15px;
  font-size: 18px;
  text-align: center;
}
